* {
    margin: 0;
    box-sizing: border-box;
    padding: 0;

  }

.header_link {
  width: 44px;
  height: 44px;
  padding: 15px 14px;
  background-color: $cerulean;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;

}


.header{
background-image: url(../images/cover.jpg);
background-size: cover;
z-index: 0;
display: flex;
align-items: center;
justify-content: center;
width: 100vw;
height: 100vh;
background-blend-mode: multiply;
background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.5) 25% color #000000);

}

.Title-text{
  text-transform: uppercase;
  width: 320px;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  font-family: RobotoSlab;
  font-size: 42px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.31;
  letter-spacing: normal;
  text-align: center;
  color: $white;
  text-align: center;
  transform: translate(0, 430px);

}

.Title-text2{
  text-align: center;
  transform: translate(0, 450px);
  width: 320px;
  height: 21px;
  margin: 10px 0 45px 15px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;

}
.button_scroll_down{
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translate(50px, 470px);
    width: 45px;
    height: 45px;
    margin: 45px 122px 0 94px;
    padding: 18px 12px 15.3px 13px;
    border-radius: 22.5px;
    background-color: #ffffff;

}

.white_section{
    width: 320px;
    height: 376px;
    padding: 30px 15px 45px;
    background-color: #ffffff;

}

.Tips_Title{
    text-transform: capitalize;
    width: 190px;
    height: 30px;
    margin: 0 200px 1px 0;
    font-family: Roboto;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    letter-spacing: normal;
    color: $lighblue;
  }

 .Title_Section1{
  width: 290px;
  height: 60px;
  margin: 1px 0 15px;
  font-family: RobotoSlab;
  font-size: 24px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  color: $K;
  text-transform: capitalize;
}

.description{
    width: 290px;
    height: 120px;
    margin: 15px 0 30px;
    font-family: Roboto;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    color: $greyish-brown;
  
  }

  .rectangle{
  width: 76px;
  height: 45px;
  margin: 30px 214px 0 0;
  padding: 0 25px 0 26px;
  border-radius: 22.5px;
  border: solid 2px $C;
}

.go{
    width: 25px;
    height: 45px;
    font-family: Roboto;
    font-size: 20px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.25;
    letter-spacing: normal;
    text-align: center;
    color: $C;
  }
.blue_section{
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100vw;
  height: 624px;
  padding: 30px 35px 60px;
  background-color: $ice-blue;
}

.Title_blue_section{
    grid-column: 1 /1; 
    width: 290px;
    height: 30px;
    font-family: RobotoSlab;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.25;
    letter-spacing: normal;
    text-align: center;
    color: $K;
  }

.Subtitle_reason{
    grid-column:1 / 3 ;
    width: 184px;
    padding-top: 20px;
    padding-left: 65px;
    font-family: RobotoSlab;
    font-size: 18px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: center;
    color: $K;
  }

  .Text_reason{
    grid-column: 1 / 3;
    width: 210px;
    height: 63px;
    font-family: Roboto;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: center;
    color: $K;
    padding-left: 45px;
  }

  .botton_main{
    grid-column: 1 / 3;
    width: 184px;
    height: 45px;
    margin: 45px 53px 0;
    padding: 0 29.5px 0 30.5px;
    border-radius: 22.5px;
    background-color: $C;

  }

  .text_botton{
    width: 124px;
    height: 45px;
    font-family: Roboto;
    font-size: 20px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.25;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
  }

.nav_icon--footer{
    width: 20.6px;
    height: 24px;
    object-fit: contain;
    background-color: $C;
  }
.link--internal{
    width: 45px;
    height: 45px;
    margin: 602px 137px 442px 138px;
    padding: 11px 12.4px 10px 12px;
    box-shadow: 0 2px 4px 0 var(--k-50);
    background-color:orange;

}

